'use client'

import { RiCheckLine, RiCloseLine } from '@remixicon/react'
import { motion } from 'motion/react'
import { cx } from '@repo/shared/utils'
import { Button } from '../Button'

interface PricingCardProps {
  title: string
  description?: string
  price: number
  originalPrice?: number
  features: string[]
  excludedFeatures?: string[]
  ctaText: string
  ctaHref?: string
  disabled?: boolean
  highlighted?: boolean
  badgeText?: string
  plan?: string
}

const formatPrice = (amount: number) =>
  new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 0
  }).format(amount)

export function PricingCard({
  title,
  description,
  price,
  originalPrice,
  features,
  excludedFeatures = [],
  ctaText,
  ctaHref,
  disabled = false,
  highlighted = false,
  badgeText,
  plan
}: PricingCardProps) {
  const cardClasses = cx(
    'relative flex flex-col rounded-2xl p-6 sm:p-8',
    'ring-1 transition-shadow duration-200',
    highlighted
      ? 'bg-gradient-to-b from-indigo-50 to-white ring-2 ring-indigo-500 dark:from-indigo-950/50 dark:to-gray-900 dark:ring-indigo-400'
      : 'bg-white ring-gray-200 dark:bg-gray-900 dark:ring-gray-800'
  )

  return (
    <motion.div
      className={cardClasses}
      initial={{ opacity: 0, y: 20 }}
      transition={{ duration: 0.4, ease: 'easeOut' }}
      viewport={{ once: true }}
      whileInView={{ opacity: 1, y: 0 }}
    >
      {badgeText && (
        <div className="absolute -top-3 left-1/2 -translate-x-1/2">
          <span className="relative inline-flex items-center whitespace-nowrap rounded-full bg-gradient-to-r from-orange-500 to-red-500 px-3 py-1 font-semibold text-white text-xs shadow-lg shadow-red-500/25">
            <span className="absolute inset-0 animate-ping rounded-full bg-red-500 opacity-20" />ðŸ”¥{' '}
            {badgeText}
          </span>
        </div>
      )}

      <div className="text-center">
        <h3 className="font-bold text-gray-900 text-lg tracking-tight dark:text-gray-100">
          {title}
        </h3>

        <div className="mt-4 flex items-baseline justify-center gap-2">
          <span className="font-bold text-4xl text-gray-900 tabular-nums dark:text-gray-100">
            {formatPrice(price)}
          </span>
          {originalPrice && (
            <del className="text-gray-400 text-lg tabular-nums dark:text-gray-500">
              {formatPrice(originalPrice)}
            </del>
          )}
        </div>
      </div>

      <div className="mt-6 flex-1">
        <ul className="space-y-3 text-left">
          {features.map((feature) => (
            <li className="flex items-start gap-3" key={feature}>
              <RiCheckLine className="mt-0.5 size-5 shrink-0 text-indigo-500" />
              <span className="text-gray-600 text-sm dark:text-gray-400">{feature}</span>
            </li>
          ))}
          {excludedFeatures.map((feature) => (
            <li className="flex items-start gap-3" key={feature}>
              <RiCloseLine className="mt-0.5 size-5 shrink-0 text-gray-400" />
              <span className="text-gray-400 text-sm line-through dark:text-gray-500">
                {feature}
              </span>
            </li>
          ))}
        </ul>

        {description && (
          <p className="mt-3 flex items-start gap-3 text-gray-600 text-sm dark:text-gray-400">
            <RiCheckLine className="mt-0.5 size-5 shrink-0 text-indigo-500" />
            {description}
          </p>
        )}
      </div>

      <div className="mt-8">
        {ctaHref ? (
          <a
            aria-label={`${ctaText} - ${title}`}
            href={ctaHref}
            onClick={() => {
              if (plan) {
                import('posthog-js').then(({ default: posthog }) => {
                  posthog.capture('pricing_plan_selected', { plan })
                })
              }
            }}
            rel="noopener noreferrer"
            target="_blank"
          >
            <Button className="w-full" variant={highlighted ? 'primary' : 'secondary'}>
              {ctaText}
            </Button>
          </a>
        ) : (
          <Button aria-label={ctaText} className="w-full" disabled={disabled} variant="secondary">
            {ctaText}
          </Button>
        )}
      </div>
    </motion.div>
  )
}
